<template>
  <div class="slider_wrap">
    <div class="video_slider">
      <van-swipe :autoplay="3000">
        <van-swipe-item v-for="(item, index) in sliderList" :key="index">
          <div @click="$router.push({
                                      path: '/videoDetail',
                                      query:{id:item.id}
                                   })">
            <div class="banner">
              <img v-lazy="getImgURL(item.showImageUrl)">
            </div>
            <div class="bc">
              <img v-lazy="mask">
            </div>

            <div class="banner_info">
              <p>{{ item.name }}</p>
              <p>
                <!-- 链接到相关视频详情页 -->
                <span>{{ item.label }}</span>
                <span>{{ item.createTime ? item.createTime : '' }}</span>
              </p>
            </div>
          </div>
        </van-swipe-item>
      </van-swipe>
    </div>
  </div>
</template>

<script>
    export default {
      name:"slider",
      data(){
        return{
          mask: require('../../assets/imgs/video/mask.png'),
          sliderList: []
        }
      },

      async created(){
        let that = this;
        let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/videoSlip');
        that.sliderList = res.result.videos;
      }
    }
</script>

<style scoped lang="scss">

  // 变量声明
  $position_absolute: absolute;
  $one_percent: 100%;
  $btnBgColor: #3ECE76;

  .slider_wrap{
    width: $one_percent;
    height: 460px;
    background: #F5F5F5;
    position: fixed;
    z-index: 9;

    .video_slider{
      width: 93%;
      height: 400px;
      padding: 30px 0;
      position: fixed;
      top: 0;
      left: 25px;

      .banner, .bc{
        width: $one_percent;
        height: $one_percent;
      }

      .bc{
        position: $position_absolute;
        top: 0;
        left: 0;
      }

      .banner_info{
        position: $position_absolute;
        width: $one_percent;
        bottom: 50px;
        padding-left: 20px;
        color: #FFF;

        p:first-child{
          font-size: 33px;
          padding-bottom: 30px;
        }

        p:last-child{
          font-size: 25px;

          span:first-child{
            background: $btnBgColor;
            padding: 10px 25px;
            border-radius: 50px;
            margin-right: 30px;
          }
        }
      }
    }
  }
</style>



